import { Injectable } from '@angular/core';

@Injectable()
export class HabilitiesService {
  sistemas:Language={name:"Sistemas y aplicaciones informaticas", version:0, icon:'../assets/img/pro-icon/icons8-code-fork-50.png'}

  habilities:Hability[] = [
    {
      language:this.sistemas,
      frameworks:[
        {
          name:"Suite Microsoft Office 2013",
          icon:'../assets/img/pc-icon.png',
          version:0,
          percent:90
        },
        {
          name:"SAP",
          icon:'../assets/img/pc-icon.png',
          version:0,
          percent:70
        },
        {
          name:"DataPro",
          icon:'../assets/img/pc-icon.png',
          version:0,
          percent:65
        },
        {
          name:"AdaptaPro",
          icon:'../assets/img/pc-icon.png',
          version:0,
          percent:40
        },
      ]
    }
  ];

  constructor() { }

  getHabilities():Hability[]{
    return this.habilities;
  }

}

export interface Hability{
  language:Language,
  frameworks:Frameworks[]
}
export interface Language{
  name:string,
  version:any,
  icon:string
}
export interface Frameworks{
  name:string,
  icon:string,
  version:number,
  percent:number,
}